@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,regular,500,600,700,800,300italic,italic,500italic,600italic,700italic,800italic&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:100,200,300,regular,500,600,700,800,900,100italic,200italic,300italic,italic,500italic,600italic,700italic,800italic,900italic&display=swap);
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  font-size: inherit;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
}

.footer__info-title, .rightside-menu__title, .menu__list-link {
  font-family: "Montserrat";
  font-weight: 700;
}

.footer__info-title, .rightside-menu__title, .menu__list-link {
  font-family: "Montserrat";
  font-weight: 700;
}

html,
body {
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 27px;
  color: #5A5a5a;
  overflow-x: hidden;
  min-width: 320px;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

.footer__info-title, .rightside-menu__title, .menu__list-link {
  font-family: "Montserrat";
  font-weight: 700;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 120px;
}
.header__btn {
  background-color: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
}

.logo {
  margin: 0 auto;
}

.menu {
  padding-right: 15px;
}
.menu__list {
  display: flex;
}
.menu__list-item {
  padding-left: 35px;
  padding-right: 45px;
}
.menu__list-link {
  color: rgba(54, 56, 56, 0.9019607843);
  line-height: 22px;
}

.rightside-menu {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #fff;
  max-width: 694px;
  width: 100%;
  padding-top: 40px;
  box-shadow: -2px 3px 17px 0px #000;
  transition: all 0.5s;
}
.rightside-menu.rightside-menu--close {
  transform: translateX(500%);
  -webkit-transform: translateX(500%);
  -moz-transform: translateX(500%);
  -ms-transform: translateX(500%);
  -o-transform: translateX(500%);
}
.rightside-menu__close {
  background-color: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
  margin-left: 50px;
  margin-bottom: 60px;
}
.rightside-menu__content {
  text-align: center;
  max-width: 485px;
  margin: 0 auto;
}
.rightside-menu__logo {
  margin-bottom: 40px;
  display: inline-block;
}
.rightside-menu__title {
  color: #363838;
  font-size: 36px;
  line-height: 51px;
  position: relative;
  margin-bottom: 17px;
  padding-bottom: 17px;
}
.rightside-menu__title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  max-width: 252px;
  margin: 0 auto;
  height: 1px;
  background: #6D7278;
  border: 1px solid #979797;
}
.rightside-menu__text {
  margin-bottom: 75px;
}
.rightside-menu__img {
  max-width: 485px;
}

.footer__info-title, .menu__list-link, .rightside-menu__title {
  font-family: "Montserrat";
  font-weight: 700;
}

.footer__content {
  background-color: #DFDFDF;
  padding: 60px 0 80px;
}
.footer__inner {
  display: flex;
  justify-content: space-between;
}
.footer__info {
  width: 540px;
}
.footer__info-title {
  color: #363838;
  margin-bottom: 14px;
  line-height: 23px;
}
.footer__info-text {
  margin-bottom: 16px;
  line-height: 27px;
  max-width: 475px;
}
.footer__info-email {
  font-weight: 600;
  font-size: 14px;
  color: #363838;
  margin-bottom: 22px;
  padding-left: 30px;
  display: inline-block;
  position: relative;
}
.footer__info-email::before {
  content: "";
  background-image: url(../img/footer_mail.svg);
  position: absolute;
  top: 8px;
  left: 0;
  width: 16px;
  height: 12px;
}
.footer__form {
  display: flex;
}
.footer__form-input {
  padding: 6px 10px 6px 18px;
  background-color: transparent;
  border-radius: 21px;
  border: 1px solid rgba(54, 56, 56, 0.7);
  width: 100%;
  mix-blend-mode: normal;
  box-sizing: border-box;
}
.footer__form-input::placeholder {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 27px;
  color: #A1A1A1;
}
.footer__form-button {
  background-color: #363838;
  color: #fffff0;
  border-radius: 21px;
  border: none;
  font-weight: 600px;
  padding: 6px 12px;
  min-width: 160px;
  margin-left: 9px;
  cursor: pointer;
}
.footer__social {
  width: 125px;
  margin-right: auto;
  margin-left: 100px;
}
.footer__social-link {
  padding-left: 28px;
  position: relative;
}
.footer__social-link::before {
  content: "";
  position: absolute;
  left: 0;
  top: 3px;
  width: 16px;
  height: 16px;
}
.footer__social-link--facebook::before {
  background-image: url(../img/facebook.svg);
}
.footer__social-link--instagram::before {
  background-image: url(../img/instagram.svg);
  width: 14px;
  height: 14px;
  top: 4px;
}
.footer__social-link--pinterest::before {
  background-image: url(../img/pinterest.svg);
}
.footer__social-link--whatsapp::before {
  background-image: url(../img/whatsapp.svg);
  width: 14px;
  height: 14px;
  top: 4px;
}
.footer__social-link--youtube::before {
  background-image: url(../img/youtube.svg);
  width: 18px;
  height: 12px;
  top: 4px;
}
.footer__social-link, .footer__menu-link {
  color: #707070;
}
.footer__social-item, .footer__menu-item {
  font-weight: 600;
  font-size: 14px;
  margin-bottom: 15px;
}
.footer__menu {
  margin-right: 60px;
  width: 125px;
}
.footer__copyring {
  color: #A1A1A1;
  background-color: #363838;
  text-align: center;
  padding: 42px 0px 41px;
}